* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}
body {
	background: -webkit-linear-gradient(135deg, rgba(0, 124, 124, 0.52), rgba(10, 113, 231, 0.972));
	background: linear-gradient(-45deg, rgba(0, 124, 124, 0.52), rgba(10, 113, 231, 0.972));
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
.card__container {
	background: transparent;
	max-width: 1200px;
	margin: 0 auto;
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
}

.title {
	width: 100%;
	text-align: center;
	background: #333;
	padding: 2rem 0;
	color: aliceblue;
	text-transform: uppercase;
	font-family: 'Helvetica', serif;
}

.card {
	margin-top: 1rem;
	background: rgb(3, 47, 88);
	max-width: 400px;
	width: 250px;
	padding: 2rem 0 ;
	text-align: center;
	color: #fff;
	border-radius: 5px;
}
.card__img {
	width: 100%;
	display: inline-block;
	max-width: 150px;
}

.search-box {
	background: rgb(2, 42, 128);
	width: 100%;
	padding: 2rem;
	text-align: center;
}

.search-input{
    padding: 1rem 2rem ;
    font-size: 1.2rem;
    border: none;
    border-radius: 5px;
}
